body.admin-theme {
  background: #FFFFFF;
  color: #000000; }
  body.admin-theme .sidebar-wrapper {
    width: 280px;
    position: absolute;
    top: 0px;
    left: 0px; }
    body.admin-theme .sidebar-wrapper .brand {
      color: #000000;
      display: block;
      padding: 15px;
      text-align: center;
      border-bottom: 1px solid #EFEFEF; }
    body.admin-theme .sidebar-wrapper a {
      color: #000000; }
    body.admin-theme .sidebar-wrapper ul {
      list-style: none;
      padding: 0px;
      margin: 0px; }
  body.admin-theme .sidebar a {
    display: block;
    font-size: 0.9rem;
    padding: 15px 25px; }
  body.admin-theme .sidebar .parent {
    border-bottom: 1px solid #EFEFEF; }
  body.admin-theme .sidebar .submenu {
    margin-left: 20px; }

.content-wrapper {
  background: #F9F9F9;
  margin-left: 280px;
  border-left: 1px solid #EFEFEF; }
  .content-wrapper .top-bar {
    background: #FFFFFF; }
  .content-wrapper .content {
    padding: 20px 40px; }

.form-v1 {
  padding: 20px;
  margin-top: 20px;
